import {request} from './request.js'

// 
function testExist({key, value}) {
  return request({
    url: '/api/testexist',
    params: {key, value}
  })
}

// 
function register(data) {
  return request({
    url: '/api/register/check.html',
    method: 'post',
    data:data
  })
}

export default {
  register,
  testExist
}