// Vuex
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    // ,dom$store.state.counter
    counter: 1000,
    user: {
      username: null,
      avatar: null
    }
  },
  mutations: {
    // 
    setUser(state, user) {
      state.user.username = user.username
      state.user.avatar = user.avatar
    },
    // 
    logout(state) {
      state.user.username = null
      state.user.avatar = null
    }
  },
  getters: {
    // ,return
	powerCounter(state) {
		return state.counter * state.counter
	}
  },
  actions: {
	  // mutationthis.store.dispatch('aIncrement')
	  aIncrement(context) {
		  setTimeout(() => {
			  context.commit('increment')
		  }, 1000)
	  }
  }
  
})

export default store